import styled from 'styled-components'

export const DivContinerCursos = styled.div`
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 30px;
    margin: 30px 0;
`

export const LiTittleCurso = styled.li`
    width: 100%;
    max-width: 1280px;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    h3{
        color: skyblue;
        font-size: 16px;

        @media (min-width: 650px) {
            font-size: 25px;
        }
    }
`

export const UlContainerTemaCursos = styled.ul`
    margin-left: 50px;
`

export const LiCustom = styled.li`
    font-size: 12px;

    @media (min-width: 650px) {
        font-size: 16px
    }
`

export const DivContainerConteudoCursos = styled.div`
    display: flex;
    align-items: flex-start;
    justify-content: center;
    max-width: 1280px;
    flex-direction: column;
    gap: 10px;
    span {
        color: skyblue;
    }

    p {
        margin: 0 10px;
    }

`

export const LiTemaCursos = styled.div`
    color: skyblue;
    font-size: 12px;

    @media (min-width: 650px) {
        font-size: 16px
    }
`